@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;700;900&display=swap");
* {
  padding: 0;
  margin: 0;
}

hr {
  margin: 1rem 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: rgb(235, 235, 235);
}

header {
  text-align: center;
  background-color: rgb(66, 184, 131);
  color: #fff;
  padding: 1rem;
  margin: 1rem;
  border-radius: 1rem;
}

.mainPage {
  width: 100%;
  display: flex;
}

.card {
  width: 15rem;
  background-color: #fff;
  padding: 1rem;
  border-radius: 1rem;
}
.card__img {
  height: 14rem;
  width: 10rem;
  margin: 0 auto;
}
.card__img img {
  width: 100%;
  height: 100%;
}
.card__price {
  background-color: rgb(66, 184, 131);
  color: #fff;
  margin: 0.3rem auto;
  padding: 0.2rem 0.5rem;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
}
.card__name {
  text-align: center;
}
.card__desc {
  font-size: 0.8rem;
  text-align: center;
  padding: 0.5rem 0;
}
.card__btn {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 1.5rem;
}
.card__btn select {
  border: none;
  background-color: rgb(66, 184, 131);
  color: #fff;
  font-family: inherit;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-weight: 500;
}
.card__btn button {
  border: none;
  background-color: rgb(66, 184, 131);
  color: #fff;
  font-family: inherit;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-weight: 500;
}

.productsDisplay {
  width: 60%;
  margin: 2rem 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 2rem;
}

.cart {
  width: 40%;
  display: flex;
  flex-direction: column;
  margin: 2rem;
  font-size: 1rem;
}

.cartBox {
  display: block;
}

.itemsInCart {
  height: 4rem;
  display: flex;
  padding: 1rem;
  justify-content: center;
}
.itemsInCart__details {
  margin: auto 0;
  margin-right: 16rem;
}
.itemsInCart__price {
  margin: auto 0;
  margin-right: 4rem;
}
.itemsInCart__btn {
  border: none;
  background-color: rgb(66, 184, 131);
  color: #fff;
  font-family: inherit;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-weight: 500;
  padding: 0.1rem 2.5rem;
  font-size: 1.4rem;
}

.checkOut__btn {
  margin-top: 1rem;
}
.checkOut__btn button {
  border: none;
  background-color: rgb(66, 184, 131);
  color: #fff;
  font-family: inherit;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-weight: 500;
  margin-right: 1rem;
  font-size: 1.2rem;
}